import React, { useEffect, useMemo, useState } from 'react'
import './styles.css'
import Graph from './graph'
import { Radio } from 'antd'
import 'antd/dist/antd.css'
import AdjacencyMatrixGraph from './AdjacencyMatrixGraph'
import AdjacencyListGraph from './AdjacencyListGraph'
const limitData = [
    { skuId: 1, list: ['', '', '018'] },
    { skuId: 2, list: ['', '', '018'] },
    { skuId: 3, list: ['', '', '018'] },
    { skuId: 4, list: ['', '', '1960'] },
    { skuId: 5, list: ['', '', '61100'] },
    { skuId: 6, list: ['', '', '018'] },
    { skuId: 7, list: ['', '', '1960'] },
]
const commoditySpecs = [
    {
        title: '',
        key: 'type',
        list: ['', '', ''],
    },
    {
        title: '',
        key: 'time',
        list: ['', '', ''],
    },
    {
        title: '',
        key: 'age',
        list: ['018', '1960', '61100'],
    },
]
// const limitData = [
//     { skuId: 1, list: ['4L', '', '128G'] },
//     { skuId: 2, list: ['4L', '', '256G'] },
//     { skuId: 3, list: ['1L', '', '128G'] },
//     { skuId: 4, list: ['1L', '', '256G'] },
// ]
// const commoditySpecs = [
//     { title: '', key: 'size', list: ['4L', '1L'] },
//     { title: '', key: 'color', list: ['', ''] },
//     { title: '', key: 'weight', list: ['128G', '256G'] },
// ]
const g = new AdjacencyListGraph()
g.loadDataSource(commoditySpecs, limitData)

// 
function Matrix() {
    const [data, setData] = useState({})
    const graph = useMemo(() => {
        const g = new AdjacencyMatrixGraph()
        g.loadDataSource(commoditySpecs, limitData)
        console.log('g.tostring', g.toString())
        // const g = new AdjacencyListGraph()
        // g.loadDataSource(commoditySpecs, limitData)
        return g
    }, [])
    const [limit, setLimit] = useState(graph.getExistVertices())

    useEffect(() => {
        // 
        const vertexList = Object.keys(data)
            .map(key => data[key])
            .filter(key => key)
        const l = graph.getIntersection(vertexList)
        console.log('intersection', l)
        if (l) {
            setLimit(l)
        }
    }, [data])

    const handleChange = (e, key) => {
        const value = e.target.value
        const newData = {
            ...data,
            [key]: value,
        }
        setData(newData)
    }

    const handleClick = key => {
        // setData
        setData({
            ...data,
            [key]: '',
        })
    }

    return (
        <div className='App'>
            {commoditySpecs.map((spec, index_i) => {
                const { title, key, list } = spec
                return (
                    <div key={index_i} className=''>
                        <div>{title}</div>
                        <Radio.Group
                            value={data[key]}
                            onChange={e => handleChange(e, key)}
                            buttonStyle='solid'
                        >
                            {list.map(item => {
                                // item
                                let disable = false
                                if (
                                    data[key] !== item &&
                                    !limit.includes(item)
                                ) {
                                    disable = true
                                }
                                return (
                                    <Radio.Button
                                        onClick={() => handleClick(key)}
                                        disabled={disable}
                                        key={item}
                                        value={item}
                                    >
                                        {item}
                                    </Radio.Button>
                                )
                            })}
                        </Radio.Group>
                    </div>
                )
            })}
        </div>
    )
}

export default Matrix
