import React, { useEffect, useState } from 'react'
import { Table, Space, message, Button, Popconfirm } from 'antd'
import {
    deleteArticleToLeanCloud,
    getArticleFromLeanCloud,
    updateArticleToLeanCloud,
} from '../../../service/article'
import { useHistory } from 'react-router'
import SearchForm from './SearchForm'

const mapKeyToText = {
    BLCU: 'hsk',
    SYSU: '',
    mid: '',
    high: '',
    korea: '',
    britain: '',
}

const ArticleTable = props => {
    const { loading, dataSource, onUpdate } = props
    const [searchList, setSearchList] = useState(dataSource || [])
    const history = useHistory()
    const columns = [
        {
            title: 'No.',
            dataIndex: 'No',
            key: 'No',
        },
        {
            title: '',
            dataIndex: 'title',
            key: 'title',
        },
        {
            title: '',
            dataIndex: 'source',
            key: 'source',
            render: key => mapKeyToText[key] || key,
        },
        {
            title: '',
            dataIndex: 'nationality',
            key: 'nationality',
            render: key => mapKeyToText[key] || key,
        },
        {
            title: '',
            dataIndex: 'score',
            key: 'score',
            render: key => mapKeyToText[key] || key,
        },
        {
            title: '',
            dataIndex: 'annotationCount',
            key: 'annotationCount',
        },
        {
            title: 'Action',
            key: 'action',
            render: (text, record) => (
                <Space size='middle'>
                    <a
                        onClick={() => handleEdit(record.objectId)}
                        type='primary'
                    >
                        
                    </a>
                    <a onClick={() => handleUpdate(record.objectId)}></a>
                    <Popconfirm
                        title=''
                        onConfirm={() => handleDelete(record.objectId)}
                    >
                        <a type='primary' danger>
                            
                        </a>
                    </Popconfirm>
                </Space>
            ),
        },
    ]

    useEffect(() => {
        setSearchList(dataSource)
    }, [dataSource])
    // const getArticle = () => {
    //     let count = 0
    //     getArticleFromLeanCloud().then(
    //         res => {
    //             const data = res.map((item, index) => {
    //                 return {
    //                     objectId: item.id,
    //                     No: index + 1,
    //                     ...item.toJSON(),
    //                 }
    //             })
    //             data.forEach(item => {
    //                 count += item.article.length
    //             })
    //             onChange && onChange(count)
    //             console.log('dataList', data)
    //             setDataList(data)
    //             setSearchList(data)
    //         },
    //         () => {
    //             message.error('')
    //         }
    //     )
    // }
    // useEffect(() => {
    //     getArticle()
    // }, [update])

    const handleEdit = id => {
        history.push({
            pathname: `/edit/${id}`,
        })
    }
    const handleDelete = id => {
        console.log('', id)
        deleteArticleToLeanCloud(id)
            .then(() => {
                message.success('')
                onUpdate && onUpdate()
            })
            .catch(() => {
                message.success('')
            })
    }

    const processText = str => {
        const el = document.createElement('div')
        el.innerHTML = str
        return el.textContent
            .replace(/\n/g, '')
            .replace(/&/g, '')
            .replace(/@/g, '')
            .replace(/([a-zA-Z])\w+/g, '')
            .replace(/<.>/g, '')
            .replace(/<>/g, '')
            .replace(/(\(.*?)\)/g, match => {
                return match[1]
            })
    }

    const handleUpdate = id => {
        props.onUpdate && props.onUpdate(id)
    }

    const handleUpp = async () => {
        const dataList = dataSource
        for (let i = 1; i < dataList.length; i++) {
            const article = dataList[i]
            console.log('article', article)
            const dd = {
                article: processText(article.article),
            }
            console.log('dd', dd)

            await updateArticleToLeanCloud(article.objectId, dd)
                .then(() => {
                    console.log('i', i, '')
                })
                .catch(() => {
                    console.log('i', i, '')
                })
        }
    }

    const handleSearch = formValues => {
        console.log('', formValues)
        const dataList = dataSource
        const newData = dataList.filter(item => {
            let flag = true
            Object.keys(formValues).forEach(key => {
                if (formValues[key] && formValues[key] !== item[key]) {
                    flag = false
                }
            })
            return flag
        })
        console.log('', newData)
        setSearchList(newData)
    }

    return (
        <div>
            {/* <Button onClick={handleUpp}> </Button> */}
            <SearchForm onChange={handleSearch} />
            <Table
                loading={loading}
                bordered={true}
                columns={columns}
                dataSource={searchList}
                pagination={{
                    showTotal: total => `${total}`,
                }}
            />
        </div>
    )
}

export default ArticleTable
