import React, { useEffect } from 'react'
import { Button, Form, Input, Select } from 'antd'

const { Option } = Select
import './SearchForm.scss'

const SearchForm = props => {
    const [form] = Form.useForm()

    const handleSelectChange = (type, value) => {
        form.setFieldsValue({
            [type]: value,
        })
    }
    const onFinish = values => {
        props.onChange && props.onChange(values)
    }
    return (
        <div className='yryr-form-wrap'>
            <Form
                // {...formItemLayout}
                // labelAlign={'right'}
                className='search-form'
                layout='inline'
                form={form}
                // name='dynamic_form_nest_item'
                onFinish={onFinish}
                autoComplete='off'
            >
                <Form.Item name='title' label=''>
                    <Input />
                </Form.Item>
                {/* 
                <Form.Item name='source' label=''>
                    <Select onChange={handleSelectChange} allowClear>
                        <Option value='BLCU'>hsk</Option>
                        <Option value='SYSU'>
                            
                        </Option>
                    </Select>
                </Form.Item> */}
                <Form.Item name='score' label=''>
                    <Select onChange={handleSelectChange} allowClear>
                        <Option value='mid'></Option>
                        <Option value='high'></Option>
                    </Select>
                </Form.Item>
                <Form.Item name='nationality' label=''>
                    <Select onChange={handleSelectChange} allowClear>
                        <Option value='korea'></Option>
                        <Option value='britain'></Option>
                    </Select>
                </Form.Item>
                <Form.Item>
                    <Button type='primary' htmlType='submit'>
                        
                    </Button>
                </Form.Item>
            </Form>
        </div>
    )
}

export default SearchForm
