import { Radio, Input, Modal } from 'antd'
import { SettingOutlined } from '@ant-design/icons'
import React, { useState } from 'react'
import './BookListOper.scss'

const TextArea = Input.TextArea
function BookListOper(props) {
    const { filterIdList, onChange } = props
    const [isModalVisible, setIsModalVisible] = useState(false)
    const [count, setCount] = useState(40)
    const [filterIds, setFilterIds] = useState('')
    const [answer, setAnswer] = useState({
        key: '',
        value: '',
    })

    const handleChangeCount = e => {
        const value = e.target.value
        setCount(value)
    }
    const handleChangeId = e => {
        const value = e.target.value
        setFilterIds(value)
    }

    const showModal = () => {
        setIsModalVisible(true)
    }

    const handleOk = () => {
        onChange &&
            onChange({
                count,
                filterIds,
                answer,
            })
        setIsModalVisible(false)
    }

    const handleCancel = () => {
        setIsModalVisible(false)
    }

    const handleChangeAnswer = (e, key) => {
        const value = e.target.value
        setAnswer({
            ...answer,
            [key]: value,
        })
    }

    return (
        <div className='book-oper'>
            <SettingOutlined
                onClick={showModal}
                style={{
                    fontSize: 24,
                }}
            />
            <Modal
                title=''
                visible={isModalVisible}
                onOk={handleOk}
                onCancel={handleCancel}
            >
                <div className='btn-list'>
                    <div className='item'>
                        <h3 className='label'></h3>
                        <Radio.Group onChange={handleChangeCount} value={count}>
                            <Radio value={10}>10</Radio>
                            <Radio value={20}>20</Radio>
                            <Radio value={30}>30</Radio>
                            <Radio value={40}>40</Radio>
                        </Radio.Group>
                    </div>
                    <div className='item'>
                        <h3 className='label'></h3>
                        <Input
                            value={filterIdList}
                            placeholder='id'
                            onChange={handleChangeId}
                        />
                    </div>
                    <div className='item'>
                        <h3 className='label'></h3>
                        <Input.Group compact>
                            <Input
                                type='number'
                                style={{ width: '20%' }}
                                onChange={e => handleChangeAnswer(e, 'key')}
                                placeholder=''
                            />
                            <Input
                                style={{ width: '80%' }}
                                onChange={e => handleChangeAnswer(e, 'value')}
                                defaultValue=''
                                placeholder=''
                            />
                        </Input.Group>
                    </div>
                </div>
            </Modal>
        </div>
    )
}

export default BookListOper
