import { Alert, Button, Col, Input, Radio, Row, Form } from 'antd'
import React, { useState } from 'react'
import { formatAnswerNumber, getNumberFromLen } from '../../utils/math'
import './Calc.scss'

/**
 * {
 *    formula'', // 
 *    formatAnswer: '', // 
 *    time: '', //
 *    input: '', // inputValue
 *    errorAnalysis:
 * }
 */
// type: 1
// type: 2
// type: 3
const getData = (type = 1, count = 10) => {
    // 
    const divisionFn = () => {
        const a = getNumberFromLen(3)
        const b = getNumberFromLen(3)
        return {
            formula: (
                <div>
                    {a}
                    <div className='divider'></div>
                    {b}
                </div>
            ),
            formatAnswer: formatAnswerNumber(a / b, 3),
            // 3%
            errorAnalysis: (input, answer) => {
                return (
                    Math.abs(`0.${input}` - `0.${answer}`) / `0.${answer}` <=
                    0.03
                )
            },
        }
    }
    // 
    const divisionFn2 = () => {
        let a = getNumberFromLen(3)
        let b = getNumberFromLen(3)
        let formatAnswer = formatAnswerNumber(a / b, 2)
        while (formatAnswer / 10 < 5 || formatAnswer % 10 < 5) {
            a = getNumberFromLen(3)
            b = getNumberFromLen(3)
            formatAnswer = formatAnswerNumber(a / b, 2)
        }
        return {
            formula: (
                <div>
                    {a}
                    <div className='divider'></div>
                    {b}
                </div>
            ),
            formatAnswer: formatAnswerNumber(a / b, 2),
            // 1
            errorAnalysis: (input, answer) => Math.abs(input - answer) <= 1,
        }
    }
    // 
    const growthFn = () => {
        // 
        const a = getNumberFromLen(3)
        // 
        const b = getNumberFromLen(3) / 10
        return {
            formula: (
                <div>
                    <span>
                        {a} * {b}%
                    </span>
                    <div className='divider'></div>
                    <span>1 + {b}%</span>
                </div>
            ),
            formatAnswer: Math.round((a * (b / 100)) / (1 + b / 100)),
            // 5%
            errorAnalysis: (input, answer) =>
                Math.abs(input - answer) / answer <= 0.05,
        }
    }

    // 
    const addFn = () => {
        const a = getNumberFromLen(3)
        const b = getNumberFromLen(3)
        return {
            formula: (
                <div>
                    {a}+{b}
                </div>
            ),
            formatAnswer: a + b,
            // 
            errorAnalysis: (input, answer) => input - answer === 0,
        }
    }
    // 
    const subFn = () => {
        const a = getNumberFromLen(3)
        const b = getNumberFromLen(3)
        return {
            formula: (
                <div>
                    {a}-{b}
                </div>
            ),
            formatAnswer: a - b,
            // 
            errorAnalysis: (input, answer) => input - answer === 0,
        }
    }
    // 3%
    const mulFn = () => {
        const a = getNumberFromLen(2)
        const b = getNumberFromLen(2)
        return {
            formula: (
                <div>
                    {a} * {b}
                </div>
            ),
            formatAnswer: a * b,
            // 3%
            errorAnalysis: (input, answer) =>
                Math.abs(input - answer) / answer <= 0.03,
        }
    }
    // 3*1
    const threeMulToOneFn = () => {
        let a = getNumberFromLen(3)
        let b = getNumberFromLen(1)
        while (a % 10 < 3 || b < 3) {
            a = getNumberFromLen(3)
            b = getNumberFromLen(1)
        }
        return {
            formula: (
                <div>
                    {a} * {b}
                </div>
            ),
            formatAnswer: a * b,
            // 0
            errorAnalysis: (input, answer) => input - answer === 0,
        }
    }

    // 2*1
    const twoMulToOneFn = () => {
        let a = getNumberFromLen(2)
        let b = getNumberFromLen(1)
        while (a < 10) {
            a = getNumberFromLen(2)
            b = getNumberFromLen(1)
        }
        return {
            formula: (
                <div>
                    {a} * {b}
                </div>
            ),
            formatAnswer: a * b,
            // 3%
            errorAnalysis: (input, answer) => input - answer === 0,
        }
    }

    // 3%
    const threeMulFn = () => {
        const a = getNumberFromLen(3)
        const b = getNumberFromLen(3)
        return {
            formula: (
                <div>
                    {a} * {b}
                </div>
            ),
            formatAnswer: formatAnswerNumber(a * b, 3),
            // 3%
            errorAnalysis: (input, answer) =>
                Math.abs(input - answer) / answer <= 0.03,
        }
    }
    // 
    const fractionFn = () => {
        const map = {
            '33.3%': 3,
            '25%': 4,
            '20%': 5,
            '16.7%': 6,
            '14.3%': 7,
            '12.5%': 8,
            '11.1%': 9,
            '10%': 10,
            '9.1%': 11,
            '8.3%': 12,
            '7.7%': 13,
            '7.1%': 14,
            '6.7%': 15,
            '6.25%': 16,
            '5.9%': 17,
            '5.6%': 18,
            '5.3%': 19,
            '11%': 9.1,
            '12%': 8.3,
            '13%': 7.7,
            '14%': 7.1,
            '15%': 6.7,
            '16%': 6.25,
            '17%': 5.9,
            '18%': 5.6,
            '19%': 5.3,
        }
        const arr = Object.keys(map).sort(() => Math.random() - 0.5)
        let index = -1
        return () => {
            index += 1
            return {
                formula: <div>{arr[index]}</div>,
                formatAnswer: map[arr[index]],
                errorAnalysis: (input, answer) => input - answer === 0,
            }
        }
    }

    // 
    const mapTypeToFn = {
        1: divisionFn,
        2: growthFn,
        3: addFn,
        4: subFn,
        5: mulFn,
        6: threeMulFn,
        7: fractionFn(),
        8: divisionFn2,
        9: threeMulToOneFn,
        10: twoMulToOneFn,
    }
    const arr = []
    for (let i = 0; i < count; i++) {
        let answerObj = mapTypeToFn[type]()
        const obj = {
            input: '',
            time: '',
            ...answerObj,
        }
        arr.push(obj)
    }
    return arr
}

const defaultColumn = [
    {
        type: 'span',
        label: '',
        span: 8,
    },
    {
        type: 'input',
        label: '',
        span: 6,
    },
    {
        type: 'alert',
        label: 'answer',
        span: 6,
    },
    {
        type: 'span',
        label: 'time',
        span: 4,
    },
]

const Calc = () => {
    const [column, setColumn] = useState(defaultColumn)
    const [calcMethod, setCalcMethod] = useState(1)
    const [answerVisible, setAnswerVisible] = useState(false)
    const [acceptRate, setAcceptRate] = useState(0)
    const [data, setData] = useState(() => getData())
    window.data = data
    const [time, setTime] = useState({
        start: '',
        end: '',
    })
    const prefix = 'calc-wrap'
    const onChange = e => {
        const value = e.target.value
        const newData = getData(value)
        setData(newData)
        setCalcMethod(value)
        setAnswerVisible(false)
    }

    const handleChangeInput = (e, index) => {
        const newData = data.map((item, j) => {
            if (index === j) {
                return {
                    ...item,
                    input: e.target.value,
                }
            }
            return item
        })
        setData(newData)
    }

    const handleClickResult = () => {
        setAnswerVisible(true)
        let count = 0
        data.forEach(item => {
            const { input, formatAnswer, errorAnalysis } = item
            if (errorAnalysis(input, formatAnswer)) {
                count++
            }
        })
        setAcceptRate(count)
        setTime({
            ...time,
            end: time.start ? parseInt((Date.now() - time.start) / 1000) : 0,
        })
    }

    const handleClickInput = index => {
        if (index === 0) {
            console.log('start!!!')
            setTime({
                ...time,
                start: Date.now(),
            })
        }
    }

    const handleBlur = index => {
        const newData = data.map((item, j) => {
            if (index === j) {
                return {
                    ...item,
                    time: Date.now(),
                }
            } else {
                return item
            }
        })
        setData(newData)
    }

    const handleReset = () => {
        setData(getData(calcMethod))
        setAnswerVisible(false)
        setAcceptRate(0)
        setTime({
            start: '',
            end: '',
        })
    }
    // 24133 44424 22443 43434
    return (
        <div className={prefix}>
            <div className={`${prefix}-method`}>
                <Button onClick={handleReset}></Button>
                <Radio.Group onChange={onChange} value={calcMethod}>
                    <Radio value={1}></Radio>
                    <Radio value={2}></Radio>
                    <Radio value={3}></Radio>
                    <Radio value={4}></Radio>
                    <Radio value={5}></Radio>
                    <Radio value={6}></Radio>
                    <Radio value={7}></Radio>
                    <Radio value={8}></Radio>
                    <Radio value={9}>3*1</Radio>
                    <Radio value={10}>2*1</Radio>
                </Radio.Group>
            </div>
            <div className={`${prefix}-content`}>
                <div className='list'>
                    <Row className='list-item'>
                        {column.map((item, index) => {
                            return (
                                <Col
                                    key={index}
                                    className='list-item-num'
                                    span={item.span}
                                >
                                    {item.label}
                                </Col>
                            )
                        })}
                    </Row>
                    <Form>
                        {data.map((item, index) => {
                            const {
                                formula,
                                input,
                                formatAnswer,
                                errorAnalysis,
                            } = item
                            let consumeTime = item.time - time.start
                            if (index > 1) {
                                const preItem = data[index - 1]
                                consumeTime = item.time - preItem.time
                            }
                            return (
                                <Row className='list-item' key={index}>
                                    <Col
                                        span={8}
                                        className='list-item-num list-item-a'
                                    >
                                        {formula}
                                    </Col>
                                    <Col
                                        span={6}
                                        className='list-item-num list-item-b'
                                    >
                                        <Input
                                            type='tel'
                                            onClick={() =>
                                                handleClickInput(index)
                                            }
                                            onChange={e =>
                                                handleChangeInput(e, index)
                                            }
                                            onBlur={() => handleBlur(index)}
                                            value={input}
                                        />
                                    </Col>
                                    {answerVisible && (
                                        <Col
                                            span={6}
                                            className='list-item-num list-item-c'
                                        >
                                            <Alert
                                                message={formatAnswer}
                                                type={
                                                    errorAnalysis(
                                                        input,
                                                        formatAnswer
                                                    )
                                                        ? 'success'
                                                        : 'error'
                                                }
                                                showIcon
                                            />
                                        </Col>
                                    )}
                                    {answerVisible && (
                                        <Col
                                            span={4}
                                            className='list-item-num list-item-c'
                                        >
                                            {parseInt(consumeTime / 1000)}
                                        </Col>
                                    )}
                                </Row>
                            )
                        })}
                    </Form>
                    <Button onClick={handleClickResult}></Button>
                    <div>
                        : {acceptRate}/{data.length}
                    </div>
                    <div>: {time.end}s</div>
                </div>
            </div>
        </div>
    )
}

export default Calc
